'use client'

import React, { useState, useEffect } from 'react'
import Link from 'next/link'
import { GraduationCap, User, Menu, LogOut, LogIn } from 'lucide-react'
import { useRouter } from 'next/navigation'

export default function Navbar() {
    const router = useRouter()
    const [isLoggedIn, setIsLoggedIn] = useState(false)

    useEffect(() => {
        // 模拟检测登录态
        const status = localStorage.getItem('is_logged_in') === 'true'
        setIsLoggedIn(status)
    }, [])

    const handleLogout = () => {
        localStorage.removeItem('is_logged_in')
        localStorage.removeItem('user_role')
        setIsLoggedIn(false)
        router.push('/login')
    }

    return (
        <nav className="navbar glass">
            <Link href="/" className="logo" style={{ display: 'flex', alignItems: 'center', gap: '0.75rem', fontWeight: 'bold', fontSize: '1.5rem' }}>
                <div style={{ background: 'var(--primary)', padding: '0.5rem', borderRadius: '8px', color: 'white' }}>
                    <GraduationCap size={24} />
                </div>
                <span className="gradient-text">ResearchBridge</span>
            </Link>

            <div style={{ display: 'flex', gap: '2rem', alignItems: 'center' }}>
                <Link href="/projects" style={{ fontWeight: 500 }}>课题搜索</Link>
                <Link href="/students" style={{ fontWeight: 500 }}>人才查询</Link>

                {isLoggedIn ? (
                    <div style={{ display: 'flex', gap: '1rem', alignItems: 'center' }}>
                        <Link href="/profile" className="btn btn-glass" style={{ padding: '0.5rem 1rem', fontSize: '0.9rem' }}>
                            <User size={18} /> 个人中心
                        </Link>
                        <button onClick={handleLogout} className="btn" style={{ padding: '0.5rem', color: '#ef4444', background: 'transparent' }}>
                            <LogOut size={20} />
                        </button>
                    </div>
                ) : (
                    <Link href="/login" className="btn btn-primary" style={{ padding: '0.5rem 1.5rem', fontSize: '0.9rem' }}>
                        <LogIn size={18} /> 登录 / 统一认证
                    </Link>
                )}

                <div className="mobile-menu" style={{ display: 'none' }}>
                    <Menu />
                </div>
            </div>
        </nav>
    )
}
